.page__body {
  min-height: 100vh;
  min-width: 320px;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  margin: 0;
  padding: 0;
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  color: $side-text-color;

  @media (min-width: $desktop-width) {
    position: relative;
  }
}

.page__main--form,
.page__main--catalog {
  overflow: hidden;
}
